import React, { useState } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import TransactionRow from './TransactionRow';
import { useDispatch, useSelector } from 'react-redux';
import { globalStore } from '../../store/globalStore';

const DepositSection = () => {


	// const customerTransactions = useSelector(state => globalStore.customerTransactions)
	const customerTransactions = useSelector(state => state.cash.customerTransactions)

	let transactionRowDone = customerTransactions.map (c =>
		<TransactionRow 
			total={c.total}
			paymentSystem={c.payment_system}
			startDate={c.start_date}
		/>
	);

	
    const dispatch = useDispatch()

	const [sum, setSum] = useState('100')
	const [payMethod, setPayMethod] = useState('1')

	const addNewTransaction = (e) => {
		e.preventDefault()
		

		const transaction = {
            id: Date.now(),
            payment_system: payMethod,
			total: Number(sum),
			status: 0,
			start_date: (Date.now() / 1000 ),
		}
		dispatch({type:"ADD_TRANSACTION_ROW", payload: transaction})
		dispatch({type:"ADD_CASH", payload: transaction.total})
	}

    return (
		<div id="deposit_section" className="deposit mb50">
		<div className="container">
			<div className="row">
				<div className="col-12 col-md-6 mb30 order-2 order-md-1">
					<form className="trans_form content_holder" action="">
						<div className="title">
							Deposit
							<div className="title_tooltip" data-bs-toggle=" tooltip" data-bs-html="true" title="Comission">
								<img className="q" src="/skins/images/q.svg" alt="" />
							</div>
						</div>
						<label className="label_select" htmlFor="payment_method">
							<span>Choose youre payment method</span>
							<select 
								className="select_list" 
								name="payment_method_type" 
								id="payment_method"
								onChange={e => setPayMethod(e.target.value)}
								value={payMethod} 
							>
								<option value="1">BTC</option>
								<option value="2">USDT(TRC20)</option>
								<option value="3">Perfect Money</option>
								<option value="4">Payer</option>
							</select>
						</label>
						<div className="input_holder inputs">
							<label className="label_input" htmlFor="first_input">
								<span>Invest</span>
								<input 
									name="first_input" 
									type="number" 
									onChange={e => setSum(e.target.value)}
									value={sum} 
								/>
								<span className="in_input">BTC</span>
							</label>
							<div className="change">
								<img src="/skins/images/exchange.svg" alt="" />
							</div>
							<label className="label_input" htmlFor="first_input">
								<span>USD you receive</span>
								<input name="first_input" type="number"  value={sum} />
								<span className="in_input">USD</span>
							</label>
						</div>
						<button 
							onClick={addNewTransaction}
							className="main_button " 
							type="submit"
						>
							Deposit
						</button>
					</form>
				</div>
				{/* <!-- IF NO DEPOSITS --> */}
				{customerTransactions.length < 1 ?
					<div className="col-12 col-md-6 mb30 order-1 order-md-2">
						<div className="step_holder">
							<div className="step_holder__tiitle next">Step 1</div>
							<div className="step_holder__text">Чтобы начать зарабатывать, пополните свой депозит минимум на $100
							</div>
						</div>
					</div>
				:
					<div>
						
					</div>
				}

				{/* <!-- IF DEPOSIT --> */}
				{customerTransactions.length > 0 ?
				<div className="col-12 col-md-6 mb30 order-3 order-md-3">
					<div className="content_holder">
						<div className="title">
							Deposit transactions
						</div>

						<nav className="brand_tabs">
							<Tabs>
								<TabList>
									<div className="nav" id="nav-tab" role="tablist">
										<Tab>
											<button className="brand_tabs__link active" type="button">Completed</button>
										</Tab>
										<Tab>
											<button className="brand_tabs__link" type="button" >Pending</button>
										</Tab>
									</div>
								</TabList>
							
								<TabPanel>
									<div className="tab-content table_scroll">
										<div className=" active">
											<div className="table-responsive">



												




												<table className="table info_table">
													<tbody>
														{ transactionRowDone }
														{/* <tr>
															<td>$10000</td>
															<td>20.09.2021</td>
															<td>Bitcoin</td>
														</tr>
														<tr>
															<td>$10000</td>
															<td>25.09.2021</td>
															<td>Bitcoin</td>
														</tr> */}
													</tbody>
												</table>
											</div>
											<div className="total">
												Total: <span>$15601586.65</span>
											</div>
										</div>
									</div>
								</TabPanel>
								<TabPanel>
									<div className="tab-content table_scroll">
										<div className="">
											<div className="table-responsive">
												<table className="table info_table">
													<tbody>
														<tr>
															<td>$10000</td>
															<td>20.09.2021</td>
															<td>Bitcoin</td>
														</tr>
													</tbody>
												</table>
											</div>
											<div className="total">
												Total: <span>$0.00</span>
											</div>
										</div>
									</div>
								</TabPanel>
							</Tabs>
						</nav>
					</div>
				</div>
				:
					<div>
						
					</div>
				}
			</div>
		</div>
	</div>
	);
}

export default DepositSection;