const defaultState = {
	customers: [
		// {name: 'asd', id: 1639303680921},
	]
}


export const customerReducer = (state = defaultState , action) => {
	switch (action.type) {
		case "ADD_CUSTOMER":
			return {...state, customers: [...state.customers, action.payload]}
		case "REMOVE_CUSTOMERS":
			// фильтрем массив кустомерс 
			// функция фильтр всегда возвращает новый массив в который попадают только те объекты в которых функция колбэк вернула тру
			return  {...state, customers: state.customers.filter(customer => customer.id !== action.payload)}
		default:
			return state 
	}
}