import React from 'react';
import DepositSection from '../DepositSection/DepositSection';
import PackagesSection from '../Sections/PackagesSection';
import UserSection from '../Sections/UserSection';
import MyPackagesSection from '../Sections/MyPackagesSection';
import TokenSaleSection from '../Sections/TokenSaleSection';
import WithdrawSection from '../Sections/WithdrawSection';
import ReferalProgramSection from '../Sections/ReferalProgramSection';
import CareerProgramSection from '../Sections/CareerProgramSection';

const MainPage = () => {
    return ( 

		<main className="page profile">
			<UserSection />
			<DepositSection />
			<PackagesSection />
			<MyPackagesSection />
			<TokenSaleSection />
			<WithdrawSection />
			<ReferalProgramSection />
			<CareerProgramSection />
		</main>

    );
} 

export default MainPage;