import React, { useState } from 'react';
import { globalStore } from '../../store/globalStore';
import { useSelector } from 'react-redux';

const user = globalStore.base;
// console.log(user);

const UserSection = () => {


	const cash = useSelector(state => state.cash.cash)

    return ( 

		<div id="user_section" className="user mb50">
		<div className="container">
			<div className="row">
				<div className="col-12 col-lg-7 mb30">
					<div className="content_holder user_info">
						<div className="user_info__avatar">
							<img src="/skins/images/user2.jpg" alt="" />
						</div>
						<div className="user_info__text">
							<div className="user_info__name fz20">{user.first_name} {user.last_name}</div>
							<div className="user_info__mail fz18">{user.email}</div>
							<div className="user_info__level fz20">{user.carrier_level}</div>
							<div className="user_info__level fz20">Beginer (0%)</div>
							<div className="user_info__assets fz14">
								<div className="user_info__usd">
									<div className="user_info__label">
										<img src="/skins/images/user_usd.svg" alt="" />
										<span className="label_name">USD balance</span>
									</div>
									<div className="user_info__sum fz16">{cash}</div>

								</div>
								<div className="user_info__crypto">
									<div className="user_info__label">
										<img src="/skins/images/user_crypto.svg" alt="" />
										<span className="label_name">AFN balance</span>

									</div>
									<div className="user_info__sum fz16">121412341.2131</div>

								</div>

							</div>

						</div>

					</div>
				</div>
				<div className="col-12 col-lg-5 mb30">
					<div className="content_holder user_ref_stats">
						<img src="/skins/images/planet.png" alt="" className="planet" />
						<div className="title">
							Refferal statistic
						</div>
						<div className="user_ref_stats__row">
							<div className="user_ref_stats__name">Volume:</div>
							<div className="user_ref_stats__sum">
								100 000
								<span>USD</span>
							</div>
						</div>
						<div className="user_ref_stats__row">
							<div className="user_ref_stats__name">Structure:</div>
							<div className="user_ref_stats__sum">
								215
								<span>Partners</span>
							</div>
						</div>
						<div className="user_ref_stats__row">
							<div className="user_ref_stats__name">Assets:</div>
							<div className="user_ref_stats__sum">
								100 000<span>USD</span>
								<br /> 45654.56
								<span>AFN</span>
							</div>
						</div>
						<div className="ref_link_holder">
							<span>
						https://affluence.network/sign/sqlyODh8oqRJcFkg6cHPupHiS
					</span>
							<img className="copy" src="/skins/images/copy.svg" alt="" />
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>

    );
}

export default UserSection;