
import { globalStore } from "./globalStore";


const defaultState = {
	cash: globalStore.base.account_score,
	customerTransactions : globalStore.customerTransactions,
}
export const cashReducer = (state = defaultState , action) => {
	switch (action.type) {
		case "ADD_CASH":
			return {...state, cash: state.cash + Number(action.payload)}
		case "GET_CASH":
			return {...state, cash: state.cash - action.payload}
		
		case "ADD_TRANSACTION_ROW":
			console.log(action.payload);
			return {...state, customerTransactions: [...state.customerTransactions, action.payload]}

		default:
			return state 
	}
}



