import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import Aside from './Aside';
import { useSelector } from 'react-redux';
// import { globalStore } from '../../store/globalStore';


const Header = () => {

	const [aside, setAside] = useState(false);

	const cash = useSelector(state => state.cash.cash)

	
	

    return ( 
		<header className="header">
			<div className="container">
				<div className="top-menu">
					<div className="logo_holder">
						<a className="logo__link" href="/#">
							<img className="logo__image" src="/skins/images/logo.svg" alt="" />
						</a>
					</div>
					<div className="balance-center">
						<NavLink to="/">Главная</NavLink>
						<NavLink to="/wallets">Кошельки</NavLink>
						<div className="balance-center__nums d-sm-block">
							USD: <span>{cash}</span>
						</div>
						<div className="balance-center__nums d-sm-block">
							AFN: <span>2123986.42123</span>
						</div>
					</div>
					<div className="burger aside_open">
						<div className="top-nav" onClick={()=>setAside(true)}>
							<span href="/#">
								<svg className="ham hamRotate ham8" viewBox="0 0 100 100" width="50">
									<path className="line top"
										d="m 30,33 h 40 c 3.722839,0 7.5,3.126468 7.5,8.578427 0,5.451959 -2.727029,8.421573 -7.5,8.421573 h -20">
									</path>
									<path className="line middle" d="m 30,50 h 40"></path>
									<path className="line bottom"
										d="m 70,67 h -40 c 0,0 -7.5,-0.802118 -7.5,-8.365747 0,-7.563629 7.5,-8.634253 7.5,-8.634253 h 20">
									</path>
								</svg>
							</span>
						</div>
					</div>
				</div>
				<Aside visible={aside} setVisible={setAside} />
				<div className="aside-bg"></div>
			</div>
		</header>
    );
}

export default Header;