import React from "react";
import Slider from "react-slick";
// import PrevArrow from './PrevArrow';
// import NextArrow from './NextArrow';

export default function PackagesSlick() {
  var settings = {
	centerMode: false,
	// centerPadding: "25px",
    dots: false,
    infinite: false,
    slidesToShow: 1,
    slidesToScroll: 1,
	arrows: false,
	// prevArrow: <PrevArrow />,
	// nextArrow: <NextArrow />
  };
  
  return (

	<section className="my_packages_holder slider">
    	<Slider {...settings}>
			<div>
				<div className="package_holder">
					<div className="package_holder__value" style={{backgroundImage: "url(/skins/images/packages/card3_4.png)"}}>160%</div>
					<div className="package_holder__name">AFN package $5000</div>
					<div className="progress">
						<div className="progress_line" style={{width: "30%"}}></div>
					</div>
					<button href="#" data-target="#modal-refinement1" data-toggle="modal" className="btn-activate package_button">Details</button>
				</div>
			</div>
			<div>
				<div className="package_holder">
					<div className="package_holder__value" style={{backgroundImage: "url(/skins/images/packages/card4_4.png)"}}>120%</div>
					<div className="package_holder__name">AFN package $100</div>
					<div className="progress">
						<div className="progress_line" style={{width: "50%"}}></div>
					</div>
					<button href="#" data-target="#modal-refinement1" data-toggle="modal" className="btn-activate package_button">Details</button>
				</div>
			</div>
		</Slider>
	</section>
  );
}
