import React from "react";
import Slider from "react-slick";
// import PrevArrow from './PrevArrow';
// import NextArrow from './NextArrow';

export default function PackagesSlick() {
  var settings = {
	centerMode: true,
	centerPadding: "25px",
    dots: false,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
	arrows: false,
	// prevArrow: <PrevArrow />,
	// nextArrow: <NextArrow />
  };
  
  return (
<section className="packages_holder">
    <Slider {...settings}>
			<div>
				<div className="package_holder" style={{backgroundImage: "url(/skins/images/packages/card5_4.png)"}}>
					<div className="package_holder__name">AFN package 160%</div>
					<div className="package_holder__value">$100</div>
					<div className="package_holder__buy">Buy package</div>
					<button href="/#" data-target="#modal-refinement1" data-toggle="modal" className="btn-activate package_button">Activate</button>
				</div>
			</div>
			<div>
				<div className="package_holder" style={{backgroundImage: "url(/skins/images/packages/card1_4.png)"}}>
					<div className="package_holder__name">AFN package 160%</div>
					<div className="package_holder__value">$500</div>
					<div className="package_holder__buy">Buy package</div>
					<button href="/#" data-target="#modal-refinement1" data-toggle="modal" className="btn-activate package_button">Activate</button>
				</div>
			</div>
			<div>
				<div className="package_holder" style={{backgroundImage: "url(/skins/images/packages/card2_4.png)"}}>
					<div className="package_holder__name">AFN package 160%</div>
					<div className="package_holder__value">$1000</div>
					<div className="package_holder__buy">Buy package</div>
					<button href="/#" data-target="#modal-refinement1" data-toggle="modal" className="btn-activate package_button">Activate</button>
				</div>
			</div>
			<div>
				<div className="package_holder" style={{backgroundImage: "url(/skins/images/packages/card3_4.png)"}}>
					<div className="package_holder__name">AFN package 160%</div>
					<div className="package_holder__value">$5000</div>
					<div className="package_holder__buy">Buy package</div>
					<button href="/#" data-target="#modal-refinement1" data-toggle="modal" className="btn-activate package_button">Activate</button>
				</div>
			</div>
    </Slider>
</section>
  );
}
