import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './Aside.module.css';

const Aside = ({visible, setVisible}) => {

	const rootClasses = [s.aside_holder]
	if(visible){
		rootClasses.push(s.active);
	}

    return (
		<div 
			className={rootClasses.join(' ')} 
			onClick={()=>setVisible(false)}
		>
			<aside
				className="aside open"
				onClick={(e)=>e.stopPropagation()}
			>
				<div className="aside_close_modal"  onClick={()=>setVisible(false)}>X</div>
				<div className="user_info_menu d-none d-md-flex">
					<div className="user_info__avatar">
						<img src="/skins/images/user2.jpg" alt="" />
					</div>
					<div className="user_info__stats fz18">
						<div className="user_info__name">Konstantine Konstantinopolskiy</div>
						<div className="user_info__rank">Silver Director 27%</div>
						<div className="user_info__mail">Sergeyalibabaexpress@ua.com</div>
						<div className="user_info__assets fz14">
							<div className="user_info__assets_row">
								<img src="/skins/images/user_usd.svg" alt="" />
								<span>USD balance:</span> 123 452
							</div>
							<div className="user_info__assets_row">
								<img src="/skins/images/user_crypto.svg" alt="" />
								<span>AFN balance </span> 0.12314321
							</div>
							<div className="user_info__assets_row">
								<img src="/skins/images/user_crypto.svg" alt="" />
								<span>Packages:</span> 156898.0894 AFN / $1500
							</div>

						</div>

					</div>

				</div>
				<div className="row">
					<div className="col-12 col-md-6 col-lg-4">
						<ul className="first_menu menu">
							<li>
								<NavLink to="/wallets" onClick={()=>setVisible(false)}>
								<span className="menu_link">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#wallet"></use> */}
									</svg> Кошельки
								</span>
								</NavLink>
							</li>
							<li>
								<a className="menu_link" href="/#deposit_section">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#wallet"></use> */}
									</svg> Deposit
								</a>
							</li>
							<li>
								<a className="menu_link" href="/#packages_section">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#token"></use> */}
									</svg> Buy Tokens
								</a>
							</li>
							<li>
								<a className="menu_link" href="/#my_packages_section">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#dashboard"></use> */}
									</svg> Packages
								</a>
							</li>
							<li>
								<a className="menu_link" href="/#token_sale_section">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#token_sale"></use> */}
									</svg> Sale tokens
								</a>
							</li>
							<li>
								<a className="menu_link" href="/#withdraw_section">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#wallet"></use> */}
									</svg> Withdraw
								</a>
							</li>
							<li>
								<a className="menu_link" href="/#referal_program">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#teams"></use> */}
									</svg> Referal program
								</a>
							</li>
							<li>
								<a className="menu_link" href="/#career_program">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#career"></use> */}
									</svg> Career
								</a>
							</li>
						</ul>
					</div>
					<div className="col-12 col-md-6 col-lg-4">
						<ul className="second_menu menu">
							<li>
								<a className="menu_link" href="/{lng}/news">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#news"></use> */}
									</svg> News
								</a>
							</li>
							<li>
								<a className="menu_link" href="/{lng}/settings">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#settings"></use> */}
									</svg> Settings
								</a>
							</li>
							<li>
								<a className="menu_link" href="/{lng}/help">
									<svg width="30" height="26">
										{/* <use xlink:href="/skins/images/icons.svg#help"></use> */}
									</svg> Help
								</a>
							</li>
						</ul>
					</div>
					<div className="col-12 col-lg-4">
						<div className="third_menu">
							<div className="theme_lang">
								<div className="theme d-none">
									<span className="dark_theme">Dark</span><span className="light_theme">
										Light
									</span>
								</div>
								<div className="langs">
									<a href="/en/dashboard">En</a>
									<a href="/ru/dashboard">Ru</a>
									<a href="/tr/dashboard">Tr</a>
									<a href="/es/dashboard">Es</a>
								</div>
							</div>
							<a href="/{$lng}/user/logout" className="logout">
								logout
							</a>
						</div>
					</div>
				</div>
			</aside>
		</div>
	);
}

export default Aside;