import React from 'react';

const TransactionRow = (props) => {

	let options = { day: '2-digit', month: '2-digit', year: 'numeric' };
	let startDate = new Date(props.startDate*1000).toLocaleDateString("en-US", options);


    return (
		<tr>
			<td>${props.total}</td>
			<td>{startDate}</td>
			<td>{props.paymentSystem}</td>
		</tr>
	);
}

export default TransactionRow;