import React from 'react';
import PackagesSlick from '../PackagesSlick/PackagesSlick';

const PackagesSection = () => {
    return (
		<div id="packages_section" className="packages mb50">
		<div className="container">
			<div className="row">
				<div className="col-12 col-md-6 mb30" id="step2_holder">
					<div className="step_holder">
						<div className="step_holder__tiitle">Step 2</div>
						<div className="step_holder__text">Покупайте пакеты токенов, которые будут размораживаться ежедневно в течении года, и вы начнете получать прибыль
						</div>
					</div>
				</div>
				<div className="col-12 col-md-6 mb30" id="step2_packges">
					<div className="content_holder">
						<div className="title">
							Activate token
						</div>
						
						<PackagesSlick />

					</div>
				</div>
			</div>
		</div>
	</div>
	);
}

export default PackagesSection;